import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import NextLink from 'next/link';
import ScrollAnimation from 'react-animate-on-scroll';
import PageSection from './page-section';

const portfolios = [
    { title: "Threads", description: "Illustration", image: "/static/images/portfolio/01-small.jpg" },
    { title: "Explore", description: "Graphic Design", image: "/static/images/portfolio/02-small.jpg" },
    { title: "Finish", description: "Identity", image: "/static/images/portfolio/03-small.jpg" },
    { title: "Lines", description: "Branding", image: "/static/images/portfolio/04-small.jpg" },
    { title: "Southwest", description: "Website Design", image: "/static/images/portfolio/05-small.jpg" },
    { title: "Window", description: "Photography", image: "/static/images/portfolio/06-small.jpg" }
]

function Portfolio() {
    return (
        <React.Fragment>
            <PageSection 
                className="portfolio" 
                title="PROJEKTI" 
                description="Lorem ipsum dolor sit amet, consectetur adipiscing elit." 
                titleAnimation={{ animateIn: "fadeInDown" }} 
                descriptionAnimation={{ animateIn: "fadeInUp" }}
            >
                <Row>
                    {portfolios.map((item, idx) => <PortfolioItem key={item.title} title={item.title} description={item.description} image={item.image} index={idx} />)}
                </Row>
                <Row>
                    <Col className="d-flex justify-content-center pt-3">
                        <ScrollAnimation animateIn="fadeInUp" duration={1.5} delay={300} animateOnce>
                            <NextLink href="/projekti">
                                <a href="#contact" className="btn btn-custom btn-lg">Ostali Projekti</a>
                            </NextLink>
                        </ScrollAnimation>
                    </Col>
                </Row>
            </PageSection>

        </React.Fragment>
    )
}

function PortfolioItem({ title, description, image, index }) {
    return (
        <div className="col-sm-6 col-md-4 col-lg-4">
            <ScrollAnimation animateIn="fadeIn" duration={1.5} delay={index * 100} animateOnce>
                <div className="portfolio-item">
                    <div className="hover-bg"> <a href="" title={title}>
                        <div className="hover-text">
                            <h4>{title}</h4>
                        </div>
                        <img src={image} className="img-fluid" alt={title} /> </a> </div>
                </div>
            </ScrollAnimation>
        </div>
    )
}

export default Portfolio;